var dialog;
var sessions;
var sliderContainer;
var iframe;

var openDialog  = function () {
    document.body.style.overflow = "hidden";
    dialog.style.display="block";
    setTimeout(function(){
        sessions.classList.add("view_animation");
    }, 0);
}

var closeDialog = function () {
    document.body.style.overflow = "auto";
    dialog.style.display="none";
    sliderContainer.classList.remove("state_iframe");
    sessions.classList.remove("view_animation");
}


var initialiseDialog = function () {
    dialog = document.getElementById("paperToCallDialog");
    sessions = document.querySelector(".sessions");
    sliderContainer = document.querySelector(".slider__container");

    window.openDialog = openDialog;
    window.closeDialog = closeDialog;

    document.addEventListener('keydown', function(e) {
        if (e.keyCode == 27) {
            window.closeDialog();
        }
    });


    var submitBtn = document.getElementById("submit");
    submitBtn.addEventListener("click", function () {
        sliderContainer.classList.add("state_iframe");
        sliderContainer.scrollTo(0, 0);
    });

    setTimeout(function () {
        if (!iframe) {
            iframe = document.createElement("iframe");
            iframe.setAttribute("src", "https://ui5congermany2020.cfapps.eu10.hana.ondemand.com/");
            iframe.setAttribute("frameborder", "0");
            document.getElementById("iframeSlide").appendChild(iframe);
        }
    }, 2500);

}


module.exports = {
    initialiseDialog: initialiseDialog
}
